.navbarSelector {
    background-color: white;
    height: 0.3vw;
    width: 11vw;
    top: 4.3vw;
    transform: translateX(18.9vw);
    position: fixed;
    border-radius: 2vw;
    z-index: 99;
    transition: all 0.3s;
}

